<template>
<div>
   <Row>
     <col span="24">
      <div class="mainfrom">
          <Row>
              <Col span="10" offset="6" >
              <div class="in">
        <Form  :label-width="90" :rules="ruleValidate" :model="formValidate" ref="formValidate">
            <FormItem label=" :" label-for="title" prop="title" >
                <Input  type="text"  placeholder="" element-id="title"  v-model="formValidate.title"></Input>
           </FormItem>
             <FormItem label=" :" label-for="time" prop="time" > 
                <DatePicker type="date" split-panels placeholder="" size="default" element-id="time" v-model="formValidate.time" ></DatePicker>   
            </FormItem>
            <FormItem label=" :" label-for="time" prop="deadline" > 
                 <DatePicker type="date" split-panels placeholder="" size="default"  v-model="formValidate.deadline" ></DatePicker>  
            </FormItem>
            <FormItem label=" :" label-for="description" prop="description">
                <Input  type="textarea"  placeholder=""   element-id="description" v-model="formValidate.description"></Input>
           </FormItem>
            <FormItem label=" :" label-for="standard" prop="standard">
                <Input  type="textarea"  placeholder=""  element-id="standard" v-model="formValidate.standard"></Input>
           </FormItem>
           <FormItem label="() :" label-for="user">
                <Input  type="text"  placeholder=" @" element-id="user"></Input>
           </FormItem>
            <FormItem label="() :" label-for="Invitation">
               <Input  type="text"  placeholder=" @ 5 " element-id="Invitation"></Input>
           </FormItem>
            <FormItem label="() :" label-for="user">
               <InputNumber   placeholder=""></InputNumber><span style="margin-left:5px;font-size:14px">%</span>
           </FormItem>
            <FormItem label=" :" >
               <RadioGroup >
                  <Radio label="" >
                      <span @click="tugm"></span>
                  </Radio>
                  <Radio label=""  >
                      <span  @click="tugm2"></span>
                  </Radio>
                  <Radio label="">
                      <span  @click="tugm3"></span>
                  </Radio>
              </RadioGroup>
           
              <div v-show="item1">
                 <span ></span>
              </div>  
               <div v-show="item2">
                   <Select v-model="model16" multiple :max-tag-count="2" placeholder="">
                  <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                  </Select>
                 <span></span>
              </div>
               <div v-show="item3">
                 <span></span>
              </div>      
            </FormItem>   
           
                <div style="padding-left:100px">
                 <Button type="primary" style="margin-right:20px"  @click="handleSubmit('formValidate')"  ></Button>
                 <Button ></Button>
                  </div>
      
       
      </Form>
            </div>
              </Col>
          </Row> 
           </div>
     </Col>
   </Row>
</div>
</template>

<script>
export default {
data () {
    return {
     
         formValidate: {
                    title: '',
                    time:'',
                    deadline:'',
                    description:'',
                    standard:''
                },
         ruleValidate: {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                     time: [
                        { required: true, type: 'date', message: '', trigger: 'change' }
                    ],
                    deadline: [
                        { required: true, type: 'date', message: '', trigger: 'change' }
                    ],

                    description: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                     standard: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                },        
            item1:false,
            item2:false,
            item3:false,
              cityList: [
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    },
                   
                ],
                model16: []
        }
            },
    methods:{
         handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('');
                        
                    } else {
                        this.$Message.error();
                    }
                })
            },
             handleReset (name) {
                this.$refs[name].resetFields();
            },
      tugm(){
               this.item1=true
               this.item2=false
               this.item3=false     
      },
      tugm2(){
               this.item2=true
               this.item1=false
               this.item3=false     
       },
       tugm3(){
               this.item3=true
               this.item2=false
               this.item1=false     
       }
}
        
}
</script>

<style scoped>
.mainfrom{
    padding:40px;
    margin:43px 2px;
    background: #fff
}


</style>
